.catalog {
  width: $max-width;
  margin: 0 auto;
  padding: 50px $catalog-gap;

  display: flex;
  flex-wrap: wrap;

  &__card {
    margin-right: $card-right-margin;
    margin-bottom: 46px;

    &:nth-child(4n) {
      margin-right: 0;
    }
    &:nth-last-child(-n + 4) {
      margin-bottom: 0;
    }
  }
}
